.fileBox > .main-form {
    position: relative;
    margin: 50px auto 0;
    padding: 15px 20px;
    width: 650px;
    text-align: left;
    box-shadow: 0px 4px 16px 0px #33333314;
    border-radius: 10px;
    background-color: white;
}
.fileBox .uploadFile input{
    display: none;
    text-align: left;
}
.fileBox .uploadFile label{
    cursor: pointer;
}
.fileBox .uploadFile label span{
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;

}
.fileBox .uploadFile label .blue{
    color: #6E41E2;
}
.fileBox .uploadFile label p{
    color: #1111117A;
    font-family: Roboto;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
}
.fileBox .resetbtn{
    position: absolute;
    bottom: -50px;
    right: 0;
    background: none;
    border: 2px solid #6E41E2;
    padding: 8px 12px;
    border-radius: 4px;
    color: #6E41E2;
    cursor: pointer;
    transition: 0.5s;
}
.fileBox .resetbtn:hover{
    background-color: #6E41E2;
    color: white;
}

@media (max-width: 1023px){
    .fileBox > .main-form{
        width: 700px;
    }
}
@media (max-width: 767px){
    .fileBox > .main-form{
        width: 500px;
    }
}
@media (max-width: 600px){
    .fileBox > .main-form{
        width: 400px;
    }
}
@media (max-width: 480px){
    .fileBox > .main-form{
        width: 350px;
    }
}
@media (max-width: 375px){
    .fileBox > .main-form{
        width: 300px;
    }
}
.explain{
    margin: 20px 0 0;
    display: block;
    color: #999;
    text-align: center;
    a{
        text-decoration: none;
        color: #6E41E2;
    }
}